<template>
	<header class="header">
		<!-- 头部的第一行 -->
		<div class="top">
			<div class="container">
				<div class="loginList">
					<p>尚品汇欢迎您！</p>
					<p v-if="!userName">
						<span>请</span>
						<router-link to="/login">登录</router-link>
						<router-link to="/register" class="register">免费注册</router-link>
					</p>
					<p v-else>
						<a>{{ userName }}</a>
						<span>&nbsp;&nbsp;</span>
						<a href="javascript:;" @click="signOut">退出登录</a>
					</p>
				</div>
				<div class="typeList">
					<!-- <router-link to="/center">我的订单</router-link> -->
					<a href="javascript:" @click="myOrder">我的订单</a>
					<router-link to="/shoppingcart">我的购物车</router-link>
					<a href="javascript:;">我的尚品汇</a>
					<a href="javascript:;">尚品汇会员</a>
					<a href="javascript:;">企业采购</a>
					<a href="javascript:;">关注尚品汇</a>
					<a href="javascript:;">合作招商</a>
					<a href="javascript:;">商家后台</a>
				</div>
			</div>
		</div>
		<!--头部第二行 搜索区域-->
		<div class="bottom">
			<h1 class="logoArea">
				<router-link class="logo" title="尚品汇" to="/home">
					<img src="../../assets/logo.png" alt="" />
				</router-link>
			</h1>
			<div class="searchArea">
				<form action="" class="searchForm">
					<input type="text" id="autocomplete" class="input-error input-xxlarge" @keyup.enter="goSearch" v-model="value" />
					<button class="sui-btn btn-xlarge btn-danger" @click="goSearch" type="button">搜索</button>
				</form>
			</div>
		</div>
	</header>
</template>

<script>
export default {
	name: 'AppHeader',
	data() {
		return {
			// 接收input输入
			value: '',
		};
	},
	computed: {
		userName() {
			return this.$store.state.login.userInfo.name;
		},
	},
	methods: {
		goSearch() {
			// 当没有query参数时 this.$route.query 打印为空对象
			if (this.$route.query) {
				let location = { name: 'search', params: { keyword: this.value || undefined } };
				// 添加query参数
				location.query = this.$route.query;
				this.$router.push(location);
			}
		},
		// 退出登录
		signOut() {
			this.$confirm('确定退出登录吗?', '提示', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
				type: 'warning',
			})
				.then(async () => {
					try {
						// 清空用户数据
						await this.$store.dispatch('login/userLogOut');
						// 删除本地存储的 token
						localStorage.removeItem('token');
						// 跳转登录页
						this.$router.push('/login');
						this.$message({
							message: '退出成功，期待下次相遇！',
							type: 'success',
						});
					} catch (error) {
						this.$message({
							message: error,
							type: 'error',
						});
					}
				})
				.catch(() => {
					this.$message({
						type: 'info',
						message: '已取消',
					});
				});
		},
		// 跳转我的订单
		async myOrder() {
			try {
				await this.$store.dispatch('center/getMyOrderDataList', { page: 1, limit: 4 });
				this.$router.push('/center');
			} catch (error) {
				if (error.message === '208') {
					this.$confirm('您当前状态为游客，登录后继续结算，是否登录？', '提示', {
						confirmButtonText: '确定',
						cancelButtonText: '取消',
						type: 'warning',
					})
						.then(() => {
							this.$router.push('/login');
						})
						.catch(() => {
							this.$message({
								type: 'info',
								message: '已取消登录',
							});
						});
				}
			}
		},
	},
	mounted() {
		this.$bus.$on('clearValue', () => {
			this.value = '';
		});
	},
};
</script>

<style scoped>
.header > .top {
	background-color: #eaeaea;
	height: 30px;
	line-height: 30px;
}
.header > .top .container {
	width: 1200px;
	margin: 0 auto;
	overflow: hidden;
}
.header > .top .container .loginList {
	float: left;
}
.header > .top .container .loginList p {
	float: left;
	margin-right: 10px;
}
.header > .top .container .loginList p .register {
	border-left: 1px solid #b3aeae;
	padding: 0 5px;
	margin-left: 5px;
}
.header > .top .container .typeList {
	float: right;
}
.header > .top .container .typeList a {
	padding: 0 10px;
}
.header > .top .container .typeList a + a {
	border-left: 1px solid #b3aeae;
}
.header > .bottom {
	width: 1200px;
	margin: 0 auto;
	overflow: hidden;
}
.header > .bottom .logoArea {
	float: left;
}
.header > .bottom .logoArea .logo img {
	width: 175px;
	margin: 25px 45px;
}
.header > .bottom .searchArea {
	float: right;
	margin-top: 35px;
}
.header > .bottom .searchArea .searchForm {
	overflow: hidden;
}
.header > .bottom .searchArea .searchForm input {
	box-sizing: border-box;
	width: 490px;
	height: 32px;
	padding: 0px 4px;
	border: 2px solid #ea4a36;
	float: left;
}
.header > .bottom .searchArea .searchForm input:focus {
	outline: none;
}
.header > .bottom .searchArea .searchForm button {
	height: 32px;
	width: 68px;
	background-color: #ea4a36;
	border: none;
	color: #fff;
	float: left;
	cursor: pointer;
}
.header > .bottom .searchArea .searchForm button:focus {
	outline: none;
}
</style>
