import request from '@/api/request';

// 查询支付信息
export const paymentInfo = orderId => {
	return request({
		url: `/payment/weixin/createNative/${orderId}`,
		method: 'get',
	});
};

// 查询支付状态
export const paymentStatus = orderId => {
	return request({
		url: `/payment/weixin/queryPayStatus/${orderId}`,
		method: 'get',
	});
};
