import request from '@/api/request';

// 获取商品详细详细
export const DetailDataList = skuId => {
	return request({ url: `/item/${skuId}`, method: 'get' });
};

// 添加购物车
export const addShoppingCart = (skuId, skuNum) => {
	return request({
		url: `/cart/addToCart/${skuId}/${skuNum}`,
		method: 'post',
	});
};
