import Vue from 'vue';
// 引入VueRouter
import VueRouter from 'vue-router';
// 应用VueRouter插件
Vue.use(VueRouter);
// 引入 store （vuex)
import store from '@/store';
// 引入组件
import AppHome from '@/pages/Home';
import AppLogin from '@/pages/Login';
import AppSearch from '@/pages/Search';
import AppRegister from '@/pages/Register';
import AppDetail from '@/pages/Detail';
import ShoppingCart from '@/pages/ShoppingCart';
import trade from '@/pages/Trade';
import Pay from '@/pages/Pay';
import PaySuccess from '@/pages/PaySuccess';
import Center from '@/pages/Center';

// 解决重复点击路由报错问题
const RouterPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(to) {
	return RouterPush.call(this, to).catch(err => err);
};

// 创建并暴露路由器
const router = new VueRouter({
	routes: [
		// 主页
		{
			path: '/home',
			component: AppHome,
			meta: {
				isShowFooter: true,
				title: '尚品汇-综合购物首选',
			},
		},
		// 搜索
		{
			// 面试题

			// params参数可传可不传
			// 在占位后面加一个问号

			// 当params传递的参数是空字符串则路由跳转会出现问题
			// 使用undefined解决
			name: 'search',
			path: '/search/:keyword?',
			component: AppSearch,
			meta: {
				isShowFooter: true,
				title: '尚品汇-搜索',
			},
		},
		// 登录
		{
			path: '/login',
			component: AppLogin,
			meta: {
				isShowFooter: false,
				title: '尚品汇-登录',
			},
		},
		// 注册
		{
			path: '/register',
			component: AppRegister,
			meta: {
				isShowFooter: false,
				title: '尚品汇-注册',
			},
		},
		// 商品详情
		{
			name: 'detail',
			path: '/detail/:skuId',
			component: AppDetail,
			meta: {
				isShowFooter: true,
			},
		},
		// 购物车
		{
			path: '/shoppingcart',
			component: ShoppingCart,
			meta: {
				isShowFooter: true,
				title: '尚品汇-我的购物车',
			},
		},
		// 提交订单页
		{
			path: '/trade',
			component: trade,
			meta: {
				isShowFooter: true,
				title: '尚品汇-提交订单',
			},
		},
		// 支付页
		{
			path: '/pay',
			component: Pay,
			meta: {
				isShowFooter: true,
				title: '尚品汇-支付',
			},
		},
		// 支付成功页
		{
			path: '/paysuccess',
			component: PaySuccess,
			meta: {
				isShowFooter: true,
				title: '尚硅谷-支付成功！',
			},
		},
		// 订单页
		{
			path: '/center',
			component: Center,
			meta: {
				isShowFooter: true,
				title: '尚硅谷-我的订单',
			},
		},
		// 重定向
		{
			path: '*',
			redirect: '/home',
		},
	],
	scrollBehavior(to, from, savedPosition) {
		// 始终将页面滚动到顶部
		return { y: 0 };
	},
});

// 路由前置守卫
router.beforeEach(async (to, from, next) => {
	// 获取 token
	let token = store.state.login.token;
	// 获取用户信息
	let name = store.state.login.userInfo.name;
	// 判断是否登录 存在 token
	if (token) {
		// 当跳转路径为 login 或 register 时直接跳转首页
		if (to.path === '/login' || to.path === '/register') {
			next('/home');
		} else {
			// 判断是否存在用户信息
			if (name) {
				next();
			} else {
				try {
					// 当不存在用户信息时派发 actions 请求
					await store.dispatch('login/userInfo');
					next();
				} catch (error) {
					console.log(123);
					// 当 token 存在 且用户信息返回未登录
					// 则当前状态为账号在其他地方登录
					// 警告用户修改密码或重新登录
					if (error.message === '208') {
						// 清空持久化token
						localStorage.removeItem('token');
						// 等待清空仓库数据进行路由跳转
						await store.dispatch('login/userLogOut');
						// 路由跳转至 login
						next('/login');
					}
				}
			}
		}
	} else {
		// 未登录 游客状态
		// 当跳转路径为 pay trade paysuccess center
		if (to.path === '/pay' || to.path === '/trade' || to.path === '/paysuccess' || to.path === '/center') {
			next('/login');
		} else {
			next();
		}
	}
});

// 路由后置守卫
router.afterEach(async (to, from) => {
	// 获取自定义配置项 网页 title
	let title = router.currentRoute.meta.title;
	if (title) {
		document.title = title;
	} else {
		// 当路由跳转至 detail 时派发 actions 请求获取商品详情
		if (to.name === 'detail') {
			document.title = '正在加载...';
			try {
				await store.dispatch('detail/getDetailList', router.currentRoute.params.skuId);
				// 将网页 title 修改为商品名称
				document.title = store.state.detail.detailDataList.skuInfo.skuName;
			} catch (error) {}
		}
	}
});

// 向外暴露
export default router;
