<template>
	<div class="swiper-container">
		<div class="swiper-button-prev">&lt;</div>
		<div class="swiper-wrapper">
			<div class="swiper-slide" v-for="(item, index) in skuImageList" :key="index.id">
				<img :class="{ active: currentIndex === index }" :src="item.imgUrl" @click="changDefault(index)" />
			</div>
		</div>
		<div class="swiper-button-next">&gt;</div>
	</div>
</template>

<script>
export default {
	name: 'ImageList',
	data() {
		return {
			// 初始选中样式
			currentIndex: 0,
		};
	},
	props: ['skuImageList'],
	methods: {
		// 点击选中图片并修改大图片
		changDefault(num) {
			this.currentIndex = num;
			this.$bus.$emit('changImgUrl', this.currentIndex);
		},
	},
};
</script>

