import { myOrder } from '@/api/center';
export default {
	// 开启命名空间
	namespaced: true,
	state: {
		myOrderList: {},
	},
	mutations: {
		myOrderDataList(state, data) {
			state.myOrderList = data;
		},
	},
	actions: {
		// 我的订单列表
		async getMyOrderDataList(context, data) {
			const result = await myOrder(data.page, data.limit);
			if (result.data.code === 200) {
				context.commit('myOrderDataList', result.data.data);
				return true;
			} else {
				return Promise.reject(new Error(result.data.code));
			}
		},
	},
	getters: {
		// 订单列表
		orderList(state) {
			return state.myOrderList.records || [];
		},
		// 当前页
		current(state) {
			return state.myOrderList.current || '';
		},
		// 总页数
		pages(state) {
			return state.myOrderList.pages || '';
		},
	},
};
