import { paymentInfo, paymentStatus } from '@/api/pay';
export default {
	// 开启命名空间
	namespaced: true,
	state: {
		payInfo: {},
	},
	mutations: {
		QUERYINFO(state, data) {
			state.payInfo = data;
		},
	},
	actions: {
		// 获取支付信息
		async queryInfo(context, orderNum) {
			const result = await paymentInfo(orderNum);
			if (result.data.code === 200) {
				context.commit('QUERYINFO', result.data.data);
				return true;
			} else {
				return Promise.reject(new Error(result.data.message));
			}
		},
		// 获取支付状态
		async queryStatus(context, orderId) {
			let result = await paymentStatus(orderId);
		},
	},
	getters: {
		totalFee(state) {
			return state.payInfo.totalFee || '';
		},
	},
};
