import request from '@/api/request';

// 登录
export const loginRequest = data => {
	return request({
		url: '/user/passport/login',
		method: 'post',
		data,
	});
};

// 获取用户信息
export const getUserInfo = () => {
	return request({
		url: '/user/passport/auth/getUserInfo',
		method: 'get',
	});
};

// 退出登录
export const quit = () => {
	return request({
		url: '/user/passport/logout',
		method: 'get',
	});
};
