import { getSmsCode, registeredUsers } from '@/api/register';
export default {
	// 开启命名空间
	namespaced: true,
	state: {
		code: '',
	},
	mutations: {
		GETCODE(state, data) {
			state.code = data;
		},
	},
	actions: {
		// 获取验证码
		async getCode(context, phone) {
			let result = await getSmsCode(phone);
			if (result.data.code === 200) {
				context.commit('GETCODE', result.data.data);
			}
		},
		// 注册请求
		async SignUpNow(context, data) {
			let result = await registeredUsers(data);
			if (result.data.code === 200) {
				return 'true';
			} else {
				return Promise.reject(new Error(result.data.message));
			}
		},
	},
};
