<template>
	<div class="trade-container">
		<h3 class="title">填写并核对订单信息</h3>
		<div class="content">
			<h5 class="receive">收件人信息</h5>
			<div class="address clearFix" v-for="item in ShippingAddress" :key="item.id">
				<span class="username selected">{{ item.consignee }}</span>
				<p>
					<span class="s1">{{ item.fullAddress }}</span>
					<span class="s2">{{ item.phoneNum }}</span>
					<span class="s3" v-show="item.isDefault === '1' ? true : false">默认地址</span>
				</p>
			</div>
			<div class="line"></div>
			<h5 class="pay">支付方式</h5>
			<div class="address clearFix">
				<span class="username selected">在线支付</span>
				<span class="username" style="margin-left: 5px" @click="collect">货到付款</span>
			</div>
			<div class="line"></div>
			<h5 class="pay">送货清单</h5>
			<div class="way">
				<h5>配送方式</h5>
				<div class="info clearFix">
					<span class="s1">天天快递</span>
					<p>配送时间：预计8月10日（周三）09:00-15:00送达</p>
				</div>
			</div>
			<div class="detail">
				<h5>商品清单</h5>
				<ul class="list clearFix" v-for="(item, index) in detailArrayList" :key="index">
					<li>
						<img :src="item.imgUrl" alt="" />
					</li>
					<li>
						<p>{{ item.skuName }}</p>
						<h4>7天无理由退货</h4>
					</li>
					<li>
						<h3>{{ item.orderPrice }}</h3>
					</li>
					<li>X{{ item.skuNum }}</li>
					<li>有货</li>
				</ul>
			</div>
			<div class="bbs">
				<h5>买家留言：</h5>
				<textarea placeholder="建议留言前先与商家沟通确认" class="remarks-cont" v-model="msg"></textarea>
			</div>
			<div class="line"></div>
			<div class="bill">
				<h5>发票信息：</h5>
				<div>普通发票（电子） 个人 明细</div>
				<h5>使用优惠/抵用</h5>
			</div>
		</div>
		<div class="money clearFix">
			<ul>
				<li>
					<b
						><i>{{ OrderDataList.totalNum }}</i
						>件商品，总商品金额</b
					>
					<span>{{ OrderDataList.totalAmount }}</span>
				</li>
				<li>
					<b>返现：</b>
					<span>0.00</span>
				</li>
				<li>
					<b>运费：</b>
					<span>0.00</span>
				</li>
			</ul>
		</div>
		<div class="trade">
			<div class="price">
				应付金额:　<span>{{ OrderDataList.totalAmount }}</span>
			</div>
			<div class="receiveInfo">
				寄送至:
				<span>{{ ship.fullAddress }}</span>
				收货人：<span>{{ ship.consignee }}</span>
				<span>{{ ship.phoneNum }}</span>
			</div>
		</div>
		<div class="sub clearFix">
			<!-- <router-link class="subBtn" to="/pay" @click="submitOrder">提交订单</router-link> -->
			<a href="javascript:;" class="subBtn" @click="submitOrder">提交订单</a>
		</div>
	</div>
</template>

<script>
import { mapGetters, mapState } from 'vuex';
export default {
	name: 'Trade',
	data() {
		return {
			// 留言
			msg: '' || '空',
		};
	},
	computed: {
		...mapState('trade', ['ShippingAddress', 'OrderDataList']),
		...mapGetters('trade', ['detailArrayList']),
		ship() {
			let obj = {};
			this.ShippingAddress.forEach(e => {
				if (e.isDefault === '1') {
					obj = e;
				}
			});
			return obj;
		},
	},
	mounted() {
		// 订单页商品列表
		this.$store.dispatch('trade/getOrderDataList');
		// 判断数据是否存储在仓库
		if (this.$store.state.trade.ShippingAddress.length === 0) {
			this.$store.dispatch('trade/UserAddress');
		}
	},
	methods: {
		// 货到付款
		collect() {
			this.$message({
				message: '该支付渠道正在调试中，敬请期待。',
				type: 'warning',
			});
		},
		// 提交订单按钮
		async submitOrder() {
			try {
				// 订单编号
				let tradeNo = this.OrderDataList.tradeNo;
				// 商品数据
				let data = {
					consignee: this.ship.consignee,
					consigneeTel: this.ship.phoneNum,
					deliveryAddress: this.ship.fullAddress,
					paymentWay: 'ONLINE',
					orderComment: this.msg,
					orderDetailList: this.detailArrayList,
				};
				let result = await this.$store.dispatch('trade/sendSubmitOrder', { tradeNo, data });
				if (result) {
					this.$message({
						message: '提交订单成功！',
						type: 'success',
					});
					this.$router.push(`/pay?orderNum=${result}`);
				}
			} catch (error) {
				this.$message({
					message: error.message,
					type: 'error',
				});
			}
		},
	},
};
</script>

