<template>
	<div class="register-container">
		<!-- 注册内容 -->
		<div class="register">
			<h3>
				注册新用户
				<span class="go">我有账号，去 <router-link to="/login">登陆</router-link> </span>
			</h3>
			<div class="content">
				<label>手机号:</label>
				<input type="text" placeholder="请输入你的手机号" v-model.number="phone" @keydown="verifyPhone" />
				<span class="error-msg" v-show="errorPhone">请输入中国大陆 +86手机号</span>
			</div>
			<div class="content">
				<label>验证码:</label>
				<input type="text" placeholder="请输入验证码" v-model.number="code" @keydown="verifyCode" />
				<button @click="sendCode" class="getCode">发送验证码</button><span>{{ Captcha }}</span>
				<span class="error-msg" v-show="errorCode">请输入六位数验证码</span>
			</div>
			<div class="content">
				<label>登录密码:</label>
				<input type="text" placeholder="请输入你的登录密码" v-model="password" @keydown="verifyPassword" />
				<span class="error-msg" v-show="errorPassword">请输入6~16位长度，必须包含大小写字母和数字</span>
			</div>
			<div class="content">
				<label>确认密码:</label>
				<input type="text" placeholder="请输入确认密码" v-model="confirmThePassword" @keydown="verifyConfirmThePassword" />
				<span class="error-msg" v-show="errorConfirmThePassword">请保持密码一致</span>
			</div>
			<div class="controls">
				<input name="m1" type="checkbox" :checked="agreement" @click="verifyAgreement" />
				<span>同意协议并注册《尚品汇用户协议》</span>
				<span class="error-msg" v-show="errorAgreement">请勾选协议</span>
			</div>
			<div class="btn">
				<button @click="enroll">完成注册</button>
			</div>
		</div>

		<!-- 底部 -->
		<div class="copyright">
			<ul>
				<li>关于我们</li>
				<li>联系我们</li>
				<li>联系客服</li>
				<li>商家入驻</li>
				<li>营销中心</li>
				<li>手机尚品汇</li>
				<li>销售联盟</li>
				<li>尚品汇社区</li>
			</ul>
			<div class="address">地址：北京市昌平区宏福科技园综合楼6层</div>
			<div class="beian">京ICP备19006430号</div>
		</div>
	</div>
</template>

<script>
// lodash 防抖
import debounce from 'lodash/debounce';
export default {
	name: 'AppRegister',
	data() {
		return {
			// 收集用户数据
			// 手机号
			phone: '',
			// 验证码
			code: '',
			// 密码
			password: '',
			// 确认密码
			confirmThePassword: '',
			// 协议
			agreement: false,

			// 错误提示
			// 手机号
			errorPhone: true,
			// 验证码
			errorCode: true,
			// 密码
			errorPassword: true,
			// 确认密码
			errorConfirmThePassword: true,
			// 协议
			errorAgreement: true,

			// 验证码
			Captcha: '',
		};
	},
	computed: {
		isEnroll() {
			if (!this.errorPhone && !this.errorCode && !this.errorPassword && !this.errorConfirmThePassword && !this.errorAgreement) {
				return true;
			} else {
				return false;
			}
		},
	},
	methods: {
		// 发送验证码
		async sendCode() {
			if (!this.errorPhone) {
				await this.$store.dispatch('register/getCode', this.phone);
				// this.code = this.$store.state.register.code;
				this.Captcha = this.$store.state.register.code;
			} else {
				this.$message({
					message: '请输入正确的手机号',
					type: 'warning',
				});
			}
		},
		// 完成注册
		async enroll() {
			if (this.isEnroll) {
				try {
					const { phone, password, code } = this;
					await this.$store.dispatch('register/SignUpNow', { phone, password, code });
					this.$router.push('/login');
				} catch (error) {
					this.$message({
						message: error,
						type: 'error',
					});
				}
			} else {
				this.$message({
					message: '请填写完整信息',
					type: 'warning',
				});
			}
		},
		// 手机号验证
		verifyPhone: debounce(function () {
			let rge = /^(?:(?:\+|00)86)?1[3-9]\d{9}$/;
			if (!rge.test(this.phone)) {
				this.errorPhone = true;
			} else {
				this.errorPhone = false;
			}
		}, 500),
		// 验证码验证
		verifyCode: debounce(function () {
			let rge = /^\d{6}$/;
			console.log(rge.test(this.code));
			if (!rge.test(this.code)) {
				this.errorCode = true;
			} else {
				this.errorCode = false;
			}
		}, 500),
		// 验证密码
		verifyPassword: debounce(function () {
			let rge = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[\S]{6,16}/;
			if (!rge.test(this.password)) {
				this.errorPassword = true;
			} else {
				this.errorPassword = false;
			}
		}, 500),
		// 确认密码
		verifyConfirmThePassword: debounce(function () {
			if (this.confirmThePassword !== this.password) {
				this.errorConfirmThePassword = true;
			} else {
				this.errorConfirmThePassword = false;
			}
		}, 500),
		// 协议
		verifyAgreement() {
			this.agreement = !this.agreement;
			if (!this.agreement) {
				this.errorAgreement = true;
			} else {
				this.errorAgreement = false;
			}
		},
	},
};
</script>

