import request from '@/api/request';

// 获取购物车列表
export const CartDataList = () => {
	return request({ url: '/cart/cartList', method: 'get' });
};

// 删除购物车商品
// url: api/cart/deleteCart/${skuId}
export const RemoveCartList = skuId => {
	return request({
		url: `/cart/deleteCart/${skuId}`,
		method: 'delete',
	});
};

// 切换商品选中状态
export const ChangChecked = (skuID, isChecked) => request({ url: `/cart/checkCart/${skuID}/${isChecked}`, method: 'get' });
