import request from '@/api/request';

// 获取短信验证码
export const getSmsCode = phone => request({ url: `/user/passport/sendCode/${phone}`, method: 'get' });

// 注册用户
export const registeredUsers = data => {
	return request({
		url: '/user/passport/register',
		method: 'post',
		data,
	});
};
