import { getUserAddress, getOrdersList, submitOrder } from '@/api/trade';
export default {
	// 开启命名空间
	namespaced: true,
	state: {
		ShippingAddress: [],
		OrderDataList: [],
	},
	mutations: {
		USERADDRESS(state, data) {
			state.ShippingAddress = data;
		},
		OrderList(state, data) {
			state.OrderDataList = data;
		},
	},
	actions: {
		// 用户收件地址
		async UserAddress(context) {
			const result = await getUserAddress();
			if (result.data.code === 200) {
				context.commit('USERADDRESS', result.data.data);
				return true;
			} else {
				return Promise.reject(new Error(result.data.code));
			}
		},
		// 订单商品列表
		async getOrderDataList(context) {
			const result = await getOrdersList();
			if (result.data.code === 200) {
				context.commit('OrderList', result.data.data);
			}
		},
		// 提交订单
		async sendSubmitOrder(context, params) {
			const result = await submitOrder(params.tradeNo, params.data);
			if (result.data.code === 200) {
				return result.data.data;
			} else {
				return Promise.reject(new Error(result.data.message));
			}
		},
	},
	getters: {
		// 商品数据列表
		detailArrayList(state) {
			return state.OrderDataList.detailArrayList || [];
		},
	},
};
